import React from "react"
import { Link } from "gatsby"
import {
    StaticImage
} from 'gatsby-plugin-image';

import { Col, Image } from "react-bootstrap"

const Blog = (props) => {
    let date = new Date(props.date).toLocaleDateString("fa-IR")
    let url = props.slug.split("/");
    url.splice(0 , 1)
    let Imadfff = "install-photoshop-on-linux-1.png" ;
    let imageSrc = "../images/" + Imadfff;
    return (
         <Col  md={6} className="post__box mt-5" key={props.key}>
                                <Link to={url[0]}>
                                    <div
                                        style = {
                                                      {
                                              backgroundImage: `url(${props.image})`,
                                              maxWidth:'347px' ,
                                              height:'192px',
                                              backgroundRepeat: 'no - repeat',
                                              backgroundSize: 'cover',
                                            }
                                        }
                                    >                    
                                    </div>    
                                        {/* {<Image  width={347} height={192} src={"image/" +props.image} />} */}
                                </Link>
                                <span className="post__box--date">{date}</span>
                                <Link to={url[0]}>
                                    <h4 className="post__box--h4">{props.title}</h4>
                                </Link>
        </Col>
    )
}

export default Blog
